@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,200;1,200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,200;1,200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,200;1,200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,200;1,200&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Navbar */
.navbar {
    height: 70px;    
}

.navbar-nav li {
    padding-right:50px;
}

.nav-link {
    font-size: 1.3em !important;
}

.nav-link:hover {
    color:gray !important;
}

.bg-light {
    background-color: rgba(96, 144, 172, 0.6)!important;
} 

.navbar-light .navbar-toggler {
    border-color: #e9ecef;
    background-color: #e9ecef;
}

.navbar-light .navbar-nav .active>.nav-link, .navbar-light .navbar-nav .nav-link.active, .navbar-light .navbar-nav .nav-link.show, .navbar-light .navbar-nav .show>.nav-link {
    color:white;
}

.navbar-light .navbar-nav .nav-link {
    color:black;
    font-family: 'Montserrat', sans-serif;
    letter-spacing: 3px;
}

#FSC {
    font-family: 'Montserrat', sans-serif;
    color: white;
    font-size: xx-large; 
    padding-left: 50px;
    padding-top: 1rem;    
}
/* Footer */

footer {
    color: rgba(12, 67, 99, 0.6);
}

footer h5 {
    text-align: center;
    font-family: 'Montserrat', sans-serif;
    letter-spacing: 2px;
}

#CopyR {
    font-family: 'Montserrat', sans-serif;
    text-align: center;
}

#MainLinks {
    color: rgba(12, 67, 99, 0.6)

}

#SitesOf {
    color: rgba(12, 67, 99, 0.6);
}
/* About Me Section */

#jumbo {
    padding-top: 20px;
}

.jumbotron {
    padding: 2rem 2rem;
    border-radius: 0;
    margin-top: 60px;
    background-color: rgba(96, 144, 172, 0.6);
}

#About {
    font-family: 'Montserrat', sans-serif;
    text-align: center;
    padding-top: 10px;
}
/* Portfolio Container */

.card {
    background-color: rgba(96, 144, 172, 0.6);
}

.card-title {
    color: rgba(57, 95, 117, 0.6);
    text-align: center;
    letter-spacing: 2px; 
       
}

a:not([href]) {
    color: white;
    text-decoration: none;  
  }
#disp4 {
    margin-top: 105px;
    font-family: Gloss and Bloom;
    letter-spacing: 2px;
    color: rgb(14, 12, 12);
    text-align: center;
}

#linksPortfolio {
    padding-top: 20px;
}

.flexbox-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
    align-content: center;
}
/* Contact Form */
#disp4 {
    margin-top: 65px;
    font-family: 'Montserrat', sans-serif;
    letter-spacing: 2px;
    color: white;
    text-align: center;
}

#disp5 {
    margin-top: -30px;
    font-family: 'Montserrat', sans-serif;
    letter-spacing: 2px;
    color: white;
    text-align: center;
}

#contact {
   
    background-size: cover;
    padding-top: 70px;
    width: 100%;
    /* height: 670px; */
    
}

.contact-form {
    background: rgba(96, 144, 172, 0.6);
    color: white;
    margin-top: 25px;
    padding: 20px;
    box-shadow: 0px 0px 10px 3px rgb(147, 176, 209);
    
}

#facebook {
    color: white;
    padding-left: 50px;
} 

#youtube {
    color: white;
    padding-left: 100px;
} 

#soundcloud {
    padding-left: 30px;
    color: white;
} 

#instagram {
    color: white;
    margin-left: 110px;
} 
.social a {
    font-size: 4.5em;
}

#socialnet {
    padding-top: 20px;
}

.fa:hover {
    opacity: 0.7;
}
@media (max-width: 1146px) {
  .carousel-caption{
      transform: translateY(-180px);
  }
}

@media (max-width: 991px) {
  #aboutmep {
      margin-top: 20px;
  }
  #portfolio {
      height: 100%;
  }
  .card {
      margin-left: 170px;
  }
  .carousel-caption {
      top: 50%;
      transform: translateY(-70%);
  }
  .carousel-caption h1 {
      font-size: 450%;
  }

  .img {
      display: block;
      margin: auto;
  }

}

@media (max-width: 768px) {
  #aboutmep {
      margin-top: 20px;
  }
  #portfolio {
      height: 100%;
  }
  .card {
      margin-left: 170px;
  }
  #contact {
      height: 100%;
  }

  #facebook {
      padding-left: 0px;

  }
  #youtube {
      padding-left: 0%;
  }
  #soundcloud {
      padding-left: 0%;
      margin-left: 0%;
  }
  #instagram {
      margin-left: 0%;
  }
}

@media (max-width: 640px) {
  .carousel-caption {
      top: 50%;
      transform: translateY(-70%);
  }
  .card {
      margin-left: 120px;
  }
}

@media (max-width: 565px) {
  .carousel-caption {
      top: 50%;
      transform: translateY(-90%);
  }
  .card {
      margin-left: 70px;
  }
}

@media (max-width: 516px) {
  .carousel-caption {
      top: 45%;
      transform: translateY(-90%);   
  }
  .carousel-caption h1 {
      font-size: 200%;
  }
  .card {
      margin-left: 0;
  }
}

@media (max-width: 400px) {
  .carousel-caption h1 {
      font-size: 150%;
  }
  #responsive {
      font-size: 130%;
  }

}
